@font-face {
  font-family: 'Lato';
  src: local('Lato'), url(./fonts/Lato-Regular.ttf) format('truetype');
}

.wrapper {
  background-color: #498CEC;
  width: 100vw;
  height: 100vh;
}

.main-container {
  max-width: 1136px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-family: Lato;
  height: 100vh;
}

.spinner {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  top: 400px;
  left: 0;
  right: 0;
  width: 100px;
  height: 100px;
  transition: 0.5s;
}

.city {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 15px 50px 15px;
  padding: 0 15px 50px 15px;
  color: #FFFFFF;
}

.city-info {
  display: flex;
  align-items: center;
  justify-content: center;
}

.city-temp {
  font-size: 180px;
}

.city-description {
  font-size: 25px;
  line-height: 30px;
  padding-bottom: 118px;
}

.city-properties {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.city-properties-first-group, .city-properties-second-group {
  display: flex;
  justify-content: space-between;
  width: 45%;
}

.city-properties-item {
  width: 50%;
}

.item__prop {
  opacity: 0.6;
  font-size: 18px;
  line-height: 22px;
  padding-bottom: 10px;
}

.item__value {
  font-size: 25px;
  line-height: 30px;
}

@media (max-width: 900px) {
  .current-city {
    font-size: 40px;
  }
  .block-choice__city, .block-choice__position {
    font-size: 14px;
  }
  .city-temp {
    font-size: 150px;
  }
  .city-icon {
    width: 50%;
  }
  .city-description {
    font-size: 20px;
  }
  .item__prop {
    font-size: 14px;
  }

  .item__value {
    font-size: 20px;
  }
}

@media (max-width: 800px) {

  .container-city {
    width: 100%;
  }
  .input-city {
    padding: 0 0 0 32px;
  }

  .city-temp {
    font-size: 130px;
  }
}

@media (max-width: 690px) {
  .wrapper {
    background-color: #7290B9;
  }

  .city-temp {
    font-size: 110px;
  }
  .temperature-toggle__btn {
    background-color: #7290B9;
    color: #8EA6C7;
    border: 1px solid #BBCADD;
  }

  .active {
    transition: 0.6s;
    background-color: #8EA6C7;
    color: #FFFFFF;
  }
  .celsius {
    border-radius: 8px 0 0 8px;
    border-right: none;
  }

  .fahrenheit {
    border-radius: 0 8px 8px 0;
    border-left: none;
  }
}

@media (max-width: 670px) {
  .container-cityList {
    width: 100%;
  }

  .head-container {
    display: block;
    padding: 50px 21px 0px 21px;
  }

  .block-info-weather {
    justify-content: space-between;
  }

  .city-properties {
    flex-direction: column;
  }

  .city-properties-first-group, .city-properties-second-group {
    width: 100%;
    padding-bottom: 10px;
    justify-content: space-around;
  }

  .city {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 25px 40px 25px;
    padding: 0 25px 40px 25px;
    color: #FFFFFF;
  }
  .city-description {
    font-size: 20px;
    line-height: 30px;
    padding-bottom: 120px;
  }
  .city-properties-item {
    padding-left: 70px;
  }
}

@media (max-width: 600px) {
  .city-temp {
    font-size: 100px;
  }
  .city-description {
    padding-bottom: 100px;
  }
}

@media (max-width: 540px) {
  .current-city {
    font-size: 25px;
  }
  .city-properties-item {
    padding-left: 30px;
  }
  .block-choice__city {
    font-size: 10px;
  }
  .block-choice__position {
    font-size: 10px;
  }
  .block-choice__img {
    margin-left: 0;
  }
}

@media (max-width: 320px) {
  .head-container {
    display: block;
    padding: 30px 21px 45px 21px;
  }
  .city-temp {
    font-size: 60px;
  }
  .block-choice__city {
    font-size: 10px;
  }
  .block-choice__position {
    font-size: 10px;
  }
  .block-choice__img {
    margin-left: 0;
  }
  .city-description {
    font-size: 20px;
    line-height: 30px;
    padding-bottom: 50px;
  }
  .current-city {
    font-size: 20px;
  }
  .city-description {
    font-size: 15px;
  }
  .item__prop {
    font-size: 10px;
  }

  .item__value {
    font-size: 15px;
  }
}



