.head-container {
  padding: 81px 15px 0px 15px;
  margin: 0 15px 0px 15px;
}

.temperature-toggle-block {
  display: flex;
  flex-wrap: wrap;
}

.temperature-toggle__deg {
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
  opacity: 0.4;
}

.temperature-toggle {
  margin-left: 9px;
}

.temperature-toggle__btn {
  border: none;
  outline: none;
  background-color: #498CEC;
  color: #92BAF4;
  border: 1px solid #92BAF4;
  height: 29px;
  width: 38px;
}

.celsius {
  border-radius: 8px 0 0 8px;
  border-right: none;
}

.fahrenheit {
  border-radius: 0 8px 8px 0;
  border-left: none;
}

.active {
  transition: 0.6s;
  background-color: #6DA3F0;
  color: #FFFFFF;
}

.block-info-weather {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.block-head {
  width: 100%;
  display: block;
}

.current-city {
  font-size: 50px;
  color: #fff;
}

.block-choice__city, .block-choice__position {
  font-size: 18px;
  color: #fff;
  opacity: 0.6;
  background: transparent;
  border: none;
}

.block-choice__img {
  font-size: 35px;
  margin-left: 22px;
}

.block-choice__city:hover {
  color: black;
  transition: 0.5s;
}

.block-choice__position:hover {
  color: black;
  transition: 0.5s;
}

@media (max-width: 900px) {

  .head-container {
    display: block;
    padding: 50px 21px 150px 21px;
  }
}





