.input-city {
  background: #FFFFFF;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  border: none;
  font-size: 30px;
  width: 100%;
  height: 97px;
  padding: 0 232px 0 32px;
  outline:none;
}

.container-city {
  position: relative;
  display: none;
}

.btn-city {
  position: absolute;
  top: 26px;
  right: 30px;
  border: none;
  background: none;
  text-transform: uppercase;
  color: #1086FF;
  font-size: 30px;
}

